/* eslint-disable prefer-promise-reject-errors */
const core = require('@actions/core')
const { scanGolangCode } = require('./languageprocessor/golangAst')
const { exec } = require('child_process')

/**
 *  Go 
 * @param {string} dirPath -  Go 
 * @returns {Array} -  Go Doc 
 */
async function scanGoCodeDirectory(dirPath) {
  try {
    console.log('build go AST')
    const download_log = await buildGoAST()
    console.log(download_log)
    console.log('scan project', dirPath)
    const result = await scanGolangCode(dirPath)
    console.log(`scan result as ${result.length}`)
    return result
  } catch (error) {
    core.error('Error happen during build go AST', error)
  }
}

/**
 *  Go 
 * @param {string} projectDir - Go 
 * @param {string} outputDir - 
 * @returns {Promise<string>} - 
 */
function buildGoAST() {
  return new Promise((resolve, reject) => {
    // 
    //  Go 
    const command = `wget https://raw.githubusercontent.com/SamYuan1990/OpenAI_CodeAgent-action/refs/heads/main/goASTBin
 -O ./goASTBin && chmod a+x goASTBin`

    // 
    exec(command, (error, stdout, stderr) => {
      if (error) {
        core.error(`fail to download go binary: ${error}`)
        reject(` Go : ${error}`)
        return
      }
      if (stderr) {
        core.error(`fail to download go binary: ${error}`)
        reject(` Go : ${stderr}`)
        return
      }

      // 
      resolve(stdout)
    })
  })
}

module.exports = {
  scanGoCodeDirectory
}
