/* eslint-disable jest/no-commented-out-tests */
/* eslint-disable prefer-promise-reject-errors */
const { exec } = require('child_process')
const fs = require('fs')
const path = require('path')
const core = require('@actions/core')

/**
 *  Go  Golang  JSON 
 * @param {string} codeDir - Golang 
 * @returns {Promise<Object>} -  JSON 
 */
function scanGolangCode(codeDir) {
  core.info(`start scanGolangCode`)
  return new Promise((resolve, reject) => {
    //  Go 
    const command = `./goASTBin ${codeDir}`
    exec(command, (error, stdout, stderr) => {
      if (error) {
        core.error(`${error.message}`)
        //reject(` Go : ${error.message}`)
        return
      }
      if (stderr) {
        core.error(`${error.message}`)
        //reject(`Go : ${stderr}`)
        return
      }

      //  JSON 
      const jsonFilePath = path.join(codeDir, 'golangAST.json')
      fs.readFile(jsonFilePath, 'utf-8', (err, data) => {
        if (err) {
          core.error(`fail to read json file ${err}`)
          //reject(` JSON : ${err.message}`)
          return
        }

        //  JSON 
        try {
          const jsonResult = JSON.parse(data)
          resolve(jsonResult)
        } catch (parseError) {
          core.error(`fail to parse data to JSON, data: ${data}`)
          core.error(`fail to parse JSON: ${parseError}`)
          //reject(` JSON : ${parseError.message}`)
        }
      })
    })
  })
}

module.exports = {
  scanGolangCode
}

